import { createApp } from 'vue';
import * as VueRouter from 'vue-router';
import  'animate.css';
import i18n from "./i18n";

import App from './App.vue';
import HomePage from "./pages/HomePage";
import PricingPage from "./pages/PricingPage";
import ContactUsPage from "./pages/ContactUsPage";
import PaymentPage from "./pages/PaymentPage";
import BusinessPage from "./pages/BusinessesPage";
import LinksPage from "./pages/LinksPage";
import AboutUsPage from "./pages/AboutUsPage";
import PaymentDemoPage from "./pages/PaymentDemoPage";

const routes = [
    {
        path: '/',
        component: HomePage
    },
    {
        path: '/pricing',
        component: PricingPage
    },
    {
        path: '/contact-us',
        component: ContactUsPage
    },
    {
        path: '/payment',
        component: PaymentPage
    },
    {
        path: '/businesses',
        component: BusinessPage
    },
    {
        path: '/links',
        component: LinksPage
    },
    {
        path: '/about-us',
        component: AboutUsPage
    },
    {
        path: '/payment-demo',
        component: PaymentDemoPage
    },
    {
        path :'/*',
        component: HomePage
    }
]

const router = VueRouter.createRouter({
    history: VueRouter.createWebHashHistory(),
    routes
})

const moneyspaceApp = createApp(App)
moneyspaceApp.use(i18n);
moneyspaceApp.use(router)
moneyspaceApp.mount('#bodyWrap')
